<template>
  <public>
    <html-head 
      title="Login"
    ></html-head>

    <section>
      <v-row>
        <v-col cols="12" sm="8" lg="5" class="mx-auto">
          <validation-observer ref="form">
          <v-form 
            :disabled="login.isLoading || login.isFetching"
            @submit.prevent="submit"
          >
            <v-card>
              <v-card-title>Login</v-card-title>
              <v-card-text>
                <form-errors :error="login.errors.message"></form-errors>
                <validation-provider vid="username" :name="'Username'" v-slot="{ errors }">
                  <v-text-field
                    :error-messages="errors"
                    autocomplete="false"
                    label="Username"
                    outlined
                    v-model="login.data.username"
                  ></v-text-field>
                </validation-provider>
                <validation-provider vid="password" :name="'Password'" v-slot="{ errors }">
                  <v-text-field
                    :error-messages="errors"
                    autocomplete="false"
                    type="password"
                    label="Password"
                    outlined
                    v-model="login.data.password"
                  ></v-text-field>
                </validation-provider>
                <v-btn 
                  :loading="login.isFetching"
                  color="primary" 
                  large 
                  type="submit"
                  width="100%" 
                >Submit</v-btn>
              </v-card-text>
            </v-card>
          </v-form>
          </validation-observer>
        </v-col>
      </v-row>
    </section>
  </public>
</template>

<script>
import Api from './routes/api'
import Login from './models/Login'
import { mapActions } from 'vuex'

export default {
  data: () => ({
    login: new Login
  }),

  methods: {
    ...mapActions({
      setAuth: 'auth/set',
    }),

    async submit () {
      const data = await this.login.request({
        method: 'post',
        url: Api.login(),
        data: this.login.data
      })

      if( !data ) 
        return this.$refs.form.setErrors( this.login.errors.details )

      this.setAuth( data )
      this.$router.push({ name: 'main' })
    }
  },

  mounted () { 
    this.login.fetch( false ) 
    this.login.load( false ) 
  }
}
</script>